import surplusWin from '../components/Vip-Surplus-Window'
import longButton from '../../components/my-components/LongButton.vue'
export default {
    components: {
        surplusWin,
        longButton
    },
    data() {
        return {
            textLength: 0,
            disabled: true,
            label: '',
            type: ''
        }
    },
    methods: {
        initPage() {
            let title = ''
            let data = this.Cacher.getData('vipDetail');
            if (data.type == 'add') {
                title = '增加' + data.label;
                this.type = '增加';
            } else {
                title = '扣除' + data.label;
                this.type = '扣除';
            }
            this.label = data.label;
            uni.setNavigationBarTitle({
                title: title
            });
        },
        getAddition(val) {
            this.textLength = val.detail.value.length;
        },
        sure() {
            uni.navigateBack();
        },
    },
    beforeCreate() {
        uni.setNavigationBarTitle({
            title: ''
        });
    },
    onLoad() {
        this.initPage();
    },
    onShow() {
        this.initPage();
    }
}
